@font-face {
  font-family: Avenir;
  font-weight: 300;
  src: url("Avenir-Book.657c3568.ttf") format("truetype"), url("Avenir-Heavy.01d9ab74.ttf") format("truetype");
}

body {
  box-sizing: border-box;
  margin: 0 8px;
  padding-top: 20px;
}

.form {
  flex-direction: column;
  gap: 20px;
  display: flex;
  position: relative;
}

.input {
  box-sizing: border-box;
  color: #3d4e61;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  width: 100%;
  font-family: Avenir, Arial, sans-serif;
  font-weight: 300;
}

.input__big {
  height: 70px;
  padding: 0 0 0 62px;
  font-size: 16px;
  line-height: 21.86px;
}

.input__small {
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
  line-height: 19.12px;
}

.form__icon:before {
  content: "";
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
}

.form__icon--big:before {
  width: 20px;
  height: 20px;
  top: 25px;
  left: 26px;
}

.form__icon--small:before {
  width: 11px;
  height: 11px;
  bottom: 16px;
  left: 13px;
}

.input__big:hover, .input__small:hover {
  transition: box-shadow .3s;
  box-shadow: 0 4px 4px #3d4e6133;
}

.input__big:focus, .input__small:focus {
  text-shadow: 0 3px 3px #bbbec2;
  transition: box-shadow .3s;
  box-shadow: 0 4px 4px #3d4e6133;
}
